<template>
  <textarea
    v-model="text"
    @input="inputText"
    placeholder=""
    class="textarea"
  />
</template>

<script>
import { Dialog } from "vant";
export default {
  data() {
    return {
      text: "",
      maxNumber: 500,
    };
  },

  methods: {
    inputText() {
      let len = this.text.length;
      if (len > this.maxNumber) {
        Dialog({
          message: `${this.maxNumber},`,
        });
        this.text = this.text.slice(0, this.maxNumber);
        return;
      }
      this.$emit("inputText", this.text);
    },
  },
};
</script>

<style lang="scss" scoped>
.textarea {
  width: 100%;
  height: 140px;
  // border-color: gray;
  border: 1px solid gray;
  resize: none;
  border-radius: 5px;
  padding: 5px;
  font-size: 14px;
  box-sizing: border-box;
}
</style>
